import React,{useEffect,useState} from 'react';
import '../css/boxContainer.css';

const BoxContainer=()=>{
    const [data, setdata] = useState([]);
    const [state, setstate] = useState("");
    const [action,setaction]=useState("Add Task");  
    const saveValue=(event)=>{
        setstate(event.target.value);
    }
    const deleteItem=(ind)=>{
        const filterdData= data.filter((item,index)=>{
            return ind !== index;
        });
        setdata(()=>{ return filterdData});
        localStorage.setItem("localdata",JSON.stringify(filterdData));
     }
     const completeItem=(event,ind)=>{
         if(event.target.innerHTML=="Completed")
            {
                event.target.parentNode.parentNode.classList.add("completedClass");
                event.target.innerHTML="In Complete";
                event.target.parentNode.getElementsByClassName('progressbtn')[0].disabled=true;
            }
         else   
            {
                event.target.parentNode.parentNode.classList.remove("completedClass");
                event.target.innerHTML="Completed";
                event.target.parentNode.getElementsByClassName('progressbtn')[0].disabled=false;
            }
     }
     const progressItem=(event,ind)=>{
        if(event.target.innerHTML=="In Progress")
            {
                event.target.classList.add("progressClass");
                event.target.innerHTML="Mark as Paused";
            }
         else   
            {
                event.target.classList.remove("progressClass");
                event.target.innerHTML="In Progress";
            }
     }
     const editItem=(item,index)=>{
        if(state===""||state.trim()==="")
            {   
                setstate(item);
                setaction("Edit");
                deleteItem(index);
            }
        else {
            alert("Add or remove the task of input field first ")
        }
     }
    const getFormData=(event)=>{
        event.preventDefault();
        if(state !== "" && state.trim()!=="")
            {  
               setdata((olddata)=>{ 
                const olddatanew=olddata!=null?[...olddata]:[];
                olddatanew.unshift(state);
                localStorage.setItem("localdata",JSON.stringify(olddatanew));
                return olddatanew;
               });
               
            }
         else if(state.length!==0 && state.trim()===""){
            alert("Only White Spaces Not Allowed");
        }  
        setstate("");
        setaction("Add Task");
        localStorage.setItem("localdata",JSON.stringify(data)); 
    };
   useEffect(() => {
      setdata(JSON.parse(localStorage.getItem("localdata")))
   }, []);
   
      return(
        <React.Fragment>  
            <div className="box-container">
            <div className="AddEventContainer">
                    <form id="form" onSubmit={getFormData}>
                        <div className="text">
                            <input type="text" id="eventInputtext" placeholder="Enter Task" value={state} onChange={saveValue}  />
                        </div>
                        <div className="submitbtn">
                            <input type="submit" id="submitbtn" value={action}/>
                        </div>
                    </form>
                </div>
                <div className="List">
                    <ul id="listUl">
                        {   
                            data!=null?data.map((item,index)=>{
                                return(     
                                    <li key={index}>
                                        <div>
                                            <p>{item}</p>
                                        </div>
                                        <div>
                                        <button className="editbtn" onClick={()=>editItem(item,index)}>Edit</button>
                                        <button className="completedbtn" onClick={(event)=>completeItem(event,index)}>Completed</button>
                                        <button className="progressbtn" onClick={(event)=>progressItem(event,index)}>In Progress</button>
                                        <button className="deletebtn" onClick={()=>deleteItem(index)}>Delete</button>
                                        </div>
                                    </li>    
                            )
                            }):<></>
                        }
                    </ul>
                </div>
            </div>
        </React.Fragment>
        );
    
}
export default BoxContainer;